import React, { Component } from 'react';

import {
  TextInputContainer,
  TextInputLabel,
  TextInputField,
} from './styles';

class TextInput extends Component {
  render() {
    const {
      label,
      type,
    } = this.props;

    return (
      <TextInputContainer
        {...this.props}
      >
        <TextInputLabel>{label}</TextInputLabel>
        <TextInputField type={type} />
      </TextInputContainer>
    );
  }
}

export default TextInput;



