import React, { Component } from 'react';

import Button from './components/atoms/Button';
import TextInput from './components/atoms/TextInput';

class App extends Component {
  render() {
    return (
      <div>
        <h2>Button</h2>
        <Button
          label="Comprar"
        />
        <Button
          label="Editar"
          disabled
        />
        <Button
          label="Selecionar"
          ghost
        />
        <Button
          label="Detalhes"
          ghost
          small
        />
        <Button
          label="Enviar"
          round
        />
        <Button
          label="Cancelar"
          round
          ghost
        />
        <Button
          label="Comprar"
          small
        />
        <Button
          label="Comprar"
          small
          round
        />
        <Button
          label="Editar"
          disabled
          small
        />
        <h2>TextInput</h2>
        <TextInput
          label="Nome completo"
          type="text"
        />
      </div>
    );
  }
}

export default App;



