import React, { Component } from 'react';

import {
  ButtonContainer,
  ButtonLabel,
} from './styles';

class Button extends Component {
  render() {
    return (
      <ButtonContainer {...this.props}>
        <ButtonLabel>{this.props.label}</ButtonLabel>
      </ButtonContainer>
    );
  }
}

export default Button;


